.calltracking { margin-left: 30px; }

.intro { padding-bottom: 60px; position: relative; }

.intro h2 { margin-bottom: 20px; }

.intro h2 span { font-size: 18px; color: #8f9496; display: inline-block; margin-left: 25px; }

.intro p.topaz { margin-top: 50px; color: #1eb0cd; font-size: 30px; margin-bottom: 50px; }

.intro figure { margin-bottom: 50px; }

.intro figure img { margin-bottom: 15px; }

.intro a { margin-right: 25px; }

.intro hr { width: 100vw; }

.header { background: #1f282c; height: 140px; padding-top: 20px; }

.header .logo { width: 190px; display: inline-block; }

.header .search { display: inline-block; color: #ffffff; opacity: 0.5; margin-left: 30px; font-size: 16px; cursor: pointer; transition: all 0.3s; }

.header .search:hover { opacity: 1; }

.header__contact p { color: #ffffff; opacity: 0.5; display: flex; align-items: center; justify-content: flex-end; font-weight: 500; margin-bottom: 10px; }

.header__contact p i { display: inline-block; margin-right: 10px; }

.header__contact a { font-weight: 500; color: #ffffff; text-decoration: none; }

.header__contact a:nth-of-type(1) { margin-right: 30px; }

.header__contact a:hover { text-decoration: underline; }

.header nav { margin-top: 30px; }

.header nav ul { display: flex; }

.header nav ul li { margin-right: 40px; }

.header nav ul li a { text-decoration: none; color: #ffffff; }

.header nav ul li a:hover { text-decoration: underline; }

.header nav ul .submenu { position: relative; }

.header nav ul .submenu:hover .submenu__block { visibility: visible; opacity: 1; }

.header nav ul .submenu__block { position: absolute; top: 36px; background-color: #444b4c; box-shadow: 0 10px 10px 0 rgba(31, 40, 44, 0.15); padding: 25px; color: #ffffff; visibility: hidden; opacity: 0; transition: all 0.3s; }

.header nav ul .submenu__block__tech { margin-bottom: 40px; }

.header nav ul .submenu__block__tech ul:last-of-type { margin-left: -13px; }

.header nav ul .submenu__block h3 { font-size: 14px; font-weight: 500; text-align: left; color: #8f9496; margin-bottom: 15px; }

.header nav ul .submenu__block ul { display: inline-block; width: 50%; }

.header nav ul .submenu__block ul li { font-size: 18px; font-weight: 500; line-height: 1; text-align: left; margin-bottom: 12px; white-space: nowrap; display: flex; align-items: center; }

.header nav ul .submenu__block ul li img { padding-right: 10px; }

.header nav ul .submenu__block ul li.plus { color: #e17352; }

.header nav ul .submenu__block ul li.plus i { position: relative; top: -2px; padding-left: 5px; }

.header__user { margin-top: 30px; display: flex; justify-content: flex-end; }

.header__user a { text-decoration: none; display: flex; align-items: center; transition: all 0.3s; }

.header__user a:hover { color: #ffffff; }

.header__user a:first-of-type { margin-right: 25px; }

.header__user a i { margin-right: 10px; }

.header__user__login { color: #1eb0cd; }

.header__user__registration { color: #f2c400; }

.footer { background: #1f282c; padding: 65px 0; color: #ffffff; }

.footer__logo { width: 150px; display: block; }

.footer__logo img { width: 100%; }

.footer__contact a { font-size: 24px; font-weight: 500; color: #ffffff; text-decoration: none; display: block; }

.footer__contact a:hover { text-decoration: underline; }

.footer__contact p { margin-top: 15px; font-size: 18px; font-weight: 500; line-height: 1.33; }

.footer__social { display: flex; flex-wrap: wrap; justify-content: flex-end; }

.footer__social p { width: 100%; font-size: 14px; text-align: right; margin-bottom: 15px; }

.footer__social a { width: 50px; height: 50px; font-size: 50px; color: #8f9496; margin-left: 15px; transition: all 0.3s; }

.footer__social a:hover { color: #1eb0cd; }

.footer__menu { margin-top: 50px; display: flex; }

.footer__menu__list { width: 17%; }

.footer__menu__list h3 { color: #8f9496; font-size: 24px; font-weight: 500; margin-bottom: 15px; }

.footer__menu__list ul li { margin-bottom: 15px; }

.footer__menu__list ul li a { color: #ffffff; text-decoration: none; font-size: 18px; }

.footer__menu__list ul li a:hover { text-decoration: underline; }

.footer__links { display: flex; margin-top: 35px; }

.footer__links__block { width: 200px; }

.footer__links__block a { color: #8f9496; font-size: 14px; text-decoration: none; display: block; white-space: nowrap; }

.footer__links__block a:hover { text-decoration: underline; }

.breadcrumbs { padding: 25px 0; }

.breadcrumbs a { color: #1eb0cd; font-size: 14px; text-decoration: none; position: relative; padding-right: 20px; }

.breadcrumbs a:hover { text-decoration: underline; }

.breadcrumbs a:before { content: ""; font-family: "iconfont"; font-size: 5px; display: inline-block; position: absolute; right: 5px; top: 50%; transform: translateY(-50%) rotate(-90deg); }

.breadcrumbs a:last-child { color: #8f9496; pointer-events: none; }

.breadcrumbs a:last-child:before { content: none; }

.aside__image { display: flex; justify-content: flex-end; margin-bottom: 30px; }

.aside__image img { width: 110px; height: 110px; }

.aside ul { border: solid 1px rgba(151, 151, 151, 0.2); padding: 25px; }

.aside ul li { margin-bottom: 7px; }

.aside ul li a { color: #444b4c; font-size: 18px; text-align: right; display: block; text-decoration: none; position: relative; }

.aside ul li a.is-active { color: #3cb0cb; }

.aside ul li a.is-active:before { content: ''; width: 8px; height: 16px; background: #3cb0cb; display: block; position: absolute; right: -25px; top: 50%; transform: translateY(-50%); }

.aside ul li a:hover:before { content: ''; width: 8px; height: 16px; background: #3cb0cb; display: block; position: absolute; right: -25px; top: 50%; transform: translateY(-50%); transition: all 0.3s; }

body { font-family: 'Museo Sans Cyrl' ,serif; font-size: 18px; }

h2 { font-size: 40px; font-weight: 500; line-height: 0.9; color: #1f282c; }

h3 { font-size: 24px; font-weight: 500; line-height: 1.25; text-align: left; color: #444b4c; }

p { font-size: 17.9px; font-weight: 500; line-height: 1.33; text-align: left; color: #444b4c; }

.flex { display: flex; }

.container { max-width: 1300px; padding: 0 10px; }

.btn { padding: 15px 25px; font-size: 18px; font-weight: 500; line-height: 1; color: #ffffff; display: inline-block; text-decoration: none; }

.btn-topaz { background: #1eb0cd; }

.btn-golden { background: #f2c400; color: #1f282c; }
